import React from "react";
import uniqid from "uniqid";
import { toast } from "react-toastify";
import InputForm from "./InputForm";
const Form = (props) => {
  const [statusOpen, setStatusOpen] = React.useState(false);
  const [item, setItem] = React.useState({
    name: "",
    amount: "",
    date: "",
    id: uniqid(), // tạo id mới
  });
  const handleOpenForm = (e) => {
    e.preventDefault();
    setStatusOpen((prevState) => !prevState);
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (item.name === "" || item.amount === "" || item.date === "") {
      toast.error("Please fill all the fields");
    } else {
      handleOpenForm(e);
      props.add({ ...item });
      setItem({
        name: "",
        amount: "",
        date: "",
        id: uniqid(), // tạo id mới
      });
    }
  };
  const handleCancel = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setItem({
      name: "",
      amount: "",
      date: "",
      id: uniqid(), // tạo id mới
    });
  };
  const handleChange = (e) => {
    setItem((prevState) => {
      return { ...prevState, [e.target.name]: e.target.value }; // lấy ra giá trị của name và gán vào state
    });
  };
  return (
    <>
      {" "}
      {!statusOpen ? (
        <>
          <div className="flex flex-row items-center justify-center pl-5 pr-5 mt-5 h-20 w-10/12 bg-violet-200 m-auto rounded-lg ">
            <button
              className="h-10 w-36 bg-purple-700 block rounded-lg text-white font-bold "
              onClick={(e) => handleOpenForm(e)}
            >
              Add Expense
            </button>
          </div>
        </>
      ) : (
          <InputForm item ={item} handleChange ={handleChange} handleCancel={handleCancel} handleSubmit={handleSubmit}></InputForm>
      )}
    </>
  );
};

export default Form;
