import React from "react";
const InputForm = ( props) => {
  return (
    <form className="w-10/12 h-1/6 bg-violet-200 mt-5 m-auto rounded-lg flex flex-wrap justify-between ">
      <input
        autoComplete="off"
        type="text"
        name="name"
        value={props.item.name}
        className="w-2/5 h-10 rounded-md bg-violet-400 mt-5 ml-5 pl-5 
                focus:outline-none placeholder:text-opacity-70 placeholder:text-white"
        placeholder="Expense Name"
        onChange={props.handleChange}
      />
      <input
        autoComplete="off"
        type="number"
        min={0.1}
        max={10000}
        name="amount"
        value={props.item.amount}
        step={0.1}
        className="w-2/5 
                h-10 rounded-md bg-violet-400 mt-5 mr-5 pl-5 focus:outline-none placeholder:text-opacity-70 placeholder:text-white"
        placeholder="Expense Amount"
        onChange={props.handleChange}
      />
      <input
        autoComplete="off"
        type="date"
        name="date"
        value={props.item.date}
        className="w-2/5 h-10 rounded-md bg-violet-400 ml-5 
                focus:outline-none pl-5 "
        onChange={props.handleChange}
      />
      <button
        className="w-32 h-10 text-center border-2 border-white active:border-black mr-5 rounded-md bg-purple-900 ml-10 text-white"
        onClick={(e) => props.handleCancel(e)}
      >
        Cancel
      </button>
      <button
        className="w-60 text-center border-2 border-white active:border-black mr-5 rounded-md bg-purple-900 h-10 text-white"
        onClick={(e) => props.handleSubmit(e)}
      >
        Add Expense
      </button>
    </form>
  );
};

export default InputForm;
