
import './App.css';
import React,{useState,useEffect}from 'react';
import { toast, Toast,ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Loader from 'react-loader-spinner';
import Container from '../components/Container/Container';
import data from '../data.json';
if(localStorage.getItem('data') === null){
  localStorage.setItem('data',JSON.stringify(data))  //kiêm tra xem localStorage có data không, chưa có thì tạo localStorage
}
function App() {
  const [dataP,setData] = useState(JSON.parse(localStorage.getItem('data')));  //dataP là data của localStorage
  const [loader,setLoader] = useState(false); //loader là state của loader khi windows đang load
  useEffect(()=>{
    localStorage.setItem('data',JSON.stringify(dataP)) //lưu dataP vào localStorage sau mỗi lần mà dataP thay đổi
  },[dataP])
   //sttchange là state của sttchange khi click vào nút thay đổi

  useEffect(() => {
   const timer = setTimeout(() => {
      setLoader(false); //khi windows load xong thì set lại loader = false
    },3000);
  }, [loader]);
  const handleAdd = (data) => {
      data = {...data,date: new Date(data.date)} //ép kiểu date
      setData(preState => [...preState,data]) //thêm data vào dataP tuy nhiên nên sử dụng chính preState  
      toast.success('Data Added Successfully') //thông báo thêm thành công
  }
  const handleDelete = (id) => {
    setData(preState => preState.filter(data => data.id !== id)) //xóa data theo id sử dựng preState , mỗi khi state thay đổi có phụ thuộc vào state trước cần sử dụng preState
    toast.success('Data Deleted Successfully')
  }
  return (
    <> {window.addEventListener('load',()=>{ //lắng nghe event load của window
      setLoader(true);
    })}
      <div className='w-screen h-screen box-border bg-slate-600 flex items-center justify-center App'> 
       {
         loader?<Loader //nếu loader = true thì hiển thị loader
         type="Puff"
         color="#00BFFF"  
         height={100}
         width={100}
         margin="auto"
       />:
       <Container items={dataP} add={handleAdd} delete={handleDelete}></Container> 
       }
      </div>
      <ToastContainer //toast container
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
      {/* Same as */}
      <ToastContainer />
    </>
  );
}

export default App;
