import React from "react"
import Form from "./Form/Form"
import ExpenseContainer from "./Expense/ExpenseContainer"
const Container = (props) => {
    return (
        <>
        <div className="w-1/2 h-full overflow-hidden  select-none bg-indigo-800 rounded-lg relative">
         <Form add ={props.add}></Form>
         <ExpenseContainer delete ={props.delete} items={props.items}></ExpenseContainer>
        </div>
        </>
    )
}

export default Container