import React, { useEffect } from "react";
import ChartBar from "./ChartBar";
import ExpenseItem from "./ExpenseItem";
import './Style.css'

const ExpenseContainer = (props) => {
  const yall = props.items.map((item) => {
    return new Date(item.date).getFullYear(); // lấy tất cả các năm
  });
  const years = [...new Set(yall)]; // lấy tất cả các năm không trùng nhau
  const [fillterbyyear, setFillterbyyear] = React.useState(
    years[years.length - 1] // lấy ra năm cuối cùng vì trong set đã được sắp xếp tháng cuối cùng gần với hiện tại nhất
  );
  const fillHandle = (e) => {
    setFillterbyyear(+e.target.value); // lấy ra năm hiện tại cần so sánh
  };
  useEffect(() => {
    if (document.getElementById("selectId").children.length === 1) {
      setFillterbyyear(years[years.length - 1]); // kiểm tra xem còn bao nhiêu năm trong dữ liệu để lấy ra nếu cuối cùng thì setFillterbyyear = years[years.length - 1]
    }
  });
  const dataFilter =props.items.filter(item =>  new Date(item.date).getFullYear() === fillterbyyear)
  
  return (
    <>
      <div className="w-10/12 min-h-1/3 max-h-6x  overflow-y-scroll scroll-smooth  bg-black rounded-lg absolute  mt-10 right-1/2 translate-x-1/2  containerEx" style={{transition: "all 0.5s ease-in-out"}}>
        <div className="flex flex-row justify-between pl-5 pr-5 mt-4">
          <span className="text-white font-bold ">Fillter by year</span>
          <select
            id="selectId"
            onClick={fillHandle} // lấy ra năm hiện tại cần so sánh
            className="w-32 rounded-sm text-center focus:outline-none "
          >
            {years.map((year, index) => {
              //render tất cả các năm trong dữ liệu
              return year !== fillterbyyear ? (
                <option key={index} value={year}>
                  {year}
                </option>
              ) : (
                <option key={index} value={year} selected>
                  {year}
                </option>
              );
            })}
          </select>
        </div>
        <ChartBar data={props.items} currently={fillterbyyear}></ChartBar>
       
        {  dataFilter.length !== 0 ? (
            dataFilter.map((item) => {
            return (
              <ExpenseItem
                delete={props.delete}
                key={item.id}
                data={item}
              ></ExpenseItem>
            );
          })
        ) : (
          <p className="text-xl mt-10 text-center text-red-600">
            No expense found.
          </p>
        )}
      </div>
    </>
  );
};

export default ExpenseContainer;
