import React from "react"
import ExpenseDate from "./ExpenseDate"
const ExpenseItem = (props) => {
    const del = (id) => {
        props.delete(id)
    }
    return (
        <>
        <div className="text-white h-14 mt-10 mb-10 w-11/12 m-auto rounded-lg bg-emerald-500 flex justify-evenly items-center" style={{transition: "all 0.5s ease-in-out"}}>
            <ExpenseDate date ={props.data.date}></ExpenseDate>
            <span className="text-3xl">{props.data.name}</span>
            <span className="min-w-1/10 pl-5 pr-5 leading-9 h-3/4 bg-purple-700 border-2 border-white rounded-lg">{`$${props.data.amount}`}</span>
            <span className="min-w-1/10 pl-5 pr-5 leading-9 h-3/4 bg-purple-700 border-2 border-white rounded-lg" onClick={ (id)=>del(props.data.id)}>Xóa</span>
            <span className="min-w-1/10 pl-5 pr-5 leading-9 h-3/4 bg-purple-700 border-2 border-white rounded-lg">Sửa</span>
            <span className="min-w-1/10 pl-5 pr-5 leading-9 h-3/4 bg-purple-700 border-2 border-white rounded-lg">Chi tiết</span>
        </div>
        </>
    )
}

export default ExpenseItem