import React from "react";
const ExpenseDate = (props) => {
  const date = new Date(props.date);
  return (
    <>
      <div className="w-36 h-full text-black font-bold flex flex-col items-center justify-center bg-rose-500 rounded-lg border-white border-2 ">
        <span className="leading-4">
          {date.toLocaleString("en-US", { month: "long" })} 
        </span>
        <span className="leading-4">
          {date.toLocaleString("en-US", { day: "2-digit" })}
        </span>
        <span className="leading-4 text-white">{date.getFullYear()}</span>
      </div>
    </>
  );
};

export default ExpenseDate;
